import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import SelectSearch from "react-select-search";

const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
class PembayaranPenjualanLM extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <div className="row">
        <div className="col-md-4 col-lg-4">
          <Field
            label="Tanggal"
            name="tanggal"
            type="date"
            component={renderField}
          />
        </div>
        <div className="col-md-4 col-lg-4 d-none">
          <Field
            label="Nama Toko"
            name="namaToko"
            type="text"
            component={renderField}
          />
        </div>
        <div className="col-md-4 col-lg-4">
          <label htmlFor="">Nama Toko</label>
          <SelectSearch
            options={this.state.items}
            search
            placeholder="Select your country"
          />
        </div>
      </div>
    );
  }
}
PembayaranPenjualanLM = reduxForm({
  form: "PembayaranPenjualanLM",
  enableReinitialize: true,
})(PembayaranPenjualanLM);
export default connect()(PembayaranPenjualanLM);
