import React, { Component, Suspense, lazy } from "react";
import { connect } from "react-redux";
import FormPembayaranPenjualanLM from "./Components/FormPembayaranPenjualanLM";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import Stepper from "react-stepper-horizontal/lib/Stepper";
import Skeleton from "react-loading-skeleton";
import Axios from "axios";
import { berhasil, gagal } from "../Alert";
import HeaderCard from "../HeaderCard";
const FormModalPembayaranPembelianLM = lazy(() =>
  import("./Element/FormModalPembayaranPembelianLM")
);

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;

const maptostate = (state) => {
  return {
    sumTotalBayar: state.provinsi.sumTotalBayar,
  };
};
const serverNonMaster = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
class PembayaranPembelianLM extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listSupplier: [],
      step: 0,
      step1: "row",
      step2: "row d-none",
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
          sort: true,
          headerStyle: () => {
            return { width: "15%" };
          },
        },
        {
          dataField: "namaAktor",
          text: "Nama Toko",
          sort: true,
          headerStyle: () => {
            return { width: "15%" };
          },
        },
        {
          dataField: "noBon",
          text: "Nomor Bon",
          sort: true,
          headerStyle: () => {
            return { width: "15%" };
          },
        },
        {
          dataField: "rp",
          text: "Bon (Rp)",
          sort: true,
          headerStyle: () => {
            return { width: "15%" };
          },
        },
        {
          dataField: "bruto",
          text: "Bon (Gr)",
          sort: true,
          headerStyle: () => {
            return { width: "15%" };
          },
        },
        {
          dataField: "aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  type="button"
                  data-toggle="modal"
                  data-target="#pembayaranLM"
                  className="btn btn-success"
                  onClick={(e) =>
                    this.setState({
                      totalBon: row.rp,
                      noBon: row.noBon,
                    })
                  }
                >
                  <i className="fa fa-check-circle mr-2"></i>
                  PILIH
                </button>
              </div>
            );
          },
          headerStyle: () => {
            return { width: "9%" };
          },
        },
      ],
      data: [
        {
          tanggal: "2020-11-10",
          noBon: "FAKTUR2020",
          totalBayar: 30000000,
        },
      ],
      listValidasiBarang: [],
    };
  }

  disabledButton = (e) => {
    e.target.disabled = true;
  };
  nextStep = () => {
    const { step } = this.state;
    if (step === 0) {
      this.setState({ step: step + 1, step2: "row", step1: "row d-none" });
    } else if (step === 1) {
      this.setState({ step: step + 1, step2: "row d-none", step3: "row" });
    }
  };

  prevStep = () => {
    const { step } = this.state;
    if (step === 1) {
      this.setState({ step: step - 1, step2: "row d-none", step1: "row" });
    } else if (step === 2) {
      this.setState({ step: step - 1, step3: "row d-none", step2: "row" });
    }
  };
  handleSubmitPembayaran(data) {
    var giro = JSON.parse(localStorage.getItem("alatBayarGiroLM"));
    var cash = JSON.parse(localStorage.getItem("alatBayarTunaiLM"));
    var transfer = JSON.parse(localStorage.getItem("alatBayarTrfLM"));
    var sumGiro = giro === null ? 0 : giro[0].giroNetto;
    var sumCash = cash === null ? 0 : cash[0].tunaiNetto;
    var sumtransfer = transfer === null ? 0 : transfer[0].transferRp;
    var simpan = {
      alatBayarTunai: JSON.parse(localStorage.getItem("alatBayarTunaiLM")),
      alatBayarTrf: JSON.parse(localStorage.getItem("alatBayarTrfLM")),
      alatBayarGiro: JSON.parse(localStorage.getItem("alatBayarGiroLM")),
      footerDocument: {
        inputBy: "ADMIN",
      },
      noFaktur: data.noBon,
      noBon: data.noBon,
      tipeTransaksi: "PEMBAYARAN-BELI-LM",
      totalAlatBayar: {
        totalTrfRp: sumCash,
        totalTunaiRp: sumtransfer,
        totalCt: 0,
        totalRsk: 0,
        totalGiroRp: sumGiro,
        totalPutusRp: 0,
        totalPutusGr: 0,
        tipeData: "-",
      },
    };
    Axios.post(serverNonMaster + "cst/dc/byr", simpan)
      .then(() => berhasil("Pembayaran berhasil"))
      .catch(() =>
        gagal("Pembayaran Gagal, Mohon periksa kembali koneksi anda")
      );
  }

  componentDidMount() {
    this.getData();
  }
  getData() {
    Axios.get(serverNonMaster + "cst/dc/lm/heads?statusValid=VALID").then(
      (res) =>
        this.setState({
          listValidasiBarang: res.data.filter(
            (list) => list.tipeTransaksi === "BELI LM"
          ),
        })
    );
  }
  render() {
    return (
      <>
        <div className="container-fluid">
          <div className="card">
            <HeaderCard title="Pembayaran Penjualan LM" />
            <div className="card-body">
              <br />
              <div className="col-lg-12 col-md-12">
                <Stepper
                  steps={[{ title: "Pilih Data" }, { title: "Konfirmasi" }]}
                  activeStep={this.state.step}
                />
              </div>
              <div className="col-lg-12">
                <div className={this.state.step1}>
                  <div className="col-lg-12">
                    <FormPembayaranPenjualanLM />
                  </div>
                  <div className="col-lg-12">
                    <ToolkitProvider
                      keyField="kodeKelompok"
                      data={this.state.listValidasiBarang}
                      columns={this.state.columns}
                      search
                    >
                      {(props) => (
                        <div className="form-group">
                          <div className="text-right mt-5 mb-3">
                            <SearchBar
                              {...props.searchProps}
                              className="form-control"
                              placeholder="Tuliskan Kode / Nama"
                              style={{ margin: 0, top: "50%" }}
                            />
                          </div>
                          <BootstrapTable
                            {...props.baseProps}
                            striped
                            hover
                            condensed
                            bordered={true}
                            headerClasses="header-table"
                            pagination={paginationFactory()}
                          />
                        </div>
                      )}
                    </ToolkitProvider>
                  </div>
                  <div className="col-lg-12">
                    <div className="text-right">
                      <button
                        className="btn btn-primary"
                        onClick={() => this.nextStep()}
                      >
                        Next
                      </button>
                    </div>
                  </div>
                </div>
                <div className={this.state.step2}>
                  <ToolkitProvider
                    keyField="kodeKelompok"
                    data={this.state.data}
                    columns={this.state.columns}
                    search
                  >
                    {(props) => (
                      <div className="form-group">
                        <div className="text-right mt-5 mb-3">
                          <SearchBar
                            {...props.searchProps}
                            className="form-control"
                            placeholder="Tuliskan Kode / Nama"
                            style={{ margin: 0, top: "50%" }}
                          />
                        </div>
                        <BootstrapTable
                          {...props.baseProps}
                          striped
                          hover
                          condensed
                          bordered={true}
                          headerClasses="header-table"
                          pagination={paginationFactory()}
                        />
                        <ExportCSVButton
                          {...props.csvProps}
                          className="btn btn-primary"
                        >
                          Export CSV
                        </ExportCSVButton>
                      </div>
                    )}
                  </ToolkitProvider>
                  <div className="col-lg-12">
                    <div className="row">
                      <div className="col-lg-6">
                        <div className="text-left">
                          <button
                            className="btn btn-primary"
                            onClick={() => this.prevStep()}
                            type="button"
                          >
                            Back
                          </button>
                        </div>
                      </div>
                      <div className="col-lg-6">
                        <div className="text-right">
                          <button className="btn btn-primary" type="submit">
                            Simpan
                          </button>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div
            id="pembayaranLM"
            className="modal fade"
            tabIndex="-1"
            role="dialog"
            aria-labelledby="primary-header-modalLabel"
            aria-hidden="true"
          >
            <div className="modal-dialog modal-full-width">
              <div className="modal-content">
                <div className="modal-header modal-colored-header bg-primary">
                  <h4 className="modal-title" id="primary-header-modalLabel">
                    Pembayara Pembelian LM
                  </h4>
                  <button
                    id="dismiss_modal"
                    name="dismiss_modal"
                    type="button"
                    className="close"
                    data-dismiss="modal"
                    aria-hidden="true"
                  >
                    Ã—
                  </button>
                </div>
                <div className="modal-body">
                  <Suspense
                    fallback={
                      <div>
                        <Skeleton width={760} height={420} />
                      </div>
                    }
                  >
                    <FormModalPembayaranPembelianLM
                      onSubmit={(data) => this.handleSubmitPembayaran(data)}
                      totalBon={this.state.totalBon}
                      sumTotalBayar={this.props.sumTotalBayar}
                      noBon={this.state.noBon}
                    />
                  </Suspense>
                </div>
                <div className="modal-footer">
                  <button
                    type="button"
                    className="btn btn-light"
                    data-dismiss="modal"
                  >
                    Close
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default connect(maptostate, null)(PembayaranPembelianLM);
